.SuperHero {
  display: flex;
  width: 80%;
  margin: 1rem auto;
  background-image: linear-gradient(151.05deg, #02ccba, #a951ed);
  flex-wrap: wrap;
  border-radius: 10px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.4);
}

.SuperHero___Image {
  width: 40%;

  box-sizing: border-box;
  padding: 1rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.SuperHero___Image img {
  width: 90%;
  height: 400px;
  border: 2px solid white;
  border-radius: 10px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
}

.SuperHero__Info {
  width: 60%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}
.SuperHero__Info .Name {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
  box-sizing: border-box;
  text-align: center;
  padding: 1rem;
  background-color: white;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.4);
  border-radius: 10px;
}
.SuperHero__Info .Name div:first-child {
  font-weight: bold;
  font-size: 1.4rem;
}
.SuperHero__Info .Name button {
  background-image: linear-gradient(
    45deg,
    rgb(0, 119, 255) 0%,
    rgb(0, 119, 255) 60%,
    rgb(0, 153, 255) 60%
  );
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  border: none;
  padding: 0.5rem;
}
.SuperHero__Info__PowerStats {
  background-color: white;
  width: 100%;
  align-items: center;
  display: flex;
  border-radius: 10px;

  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.4);

  flex-direction: column;
}
.MoreInfo {
  width: 100%;
  padding: 1rem;
  flex-wrap: wrap;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.MoreInfo > div {
  margin: 1rem 0;
  padding: 1rem;
  border-bottom: 4px solid orange;
  background-color: white;
  border-radius: 10px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4);
}
.MoreInfo span {
  font-weight: bold;
}
.PowerStatsList {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  padding: 1rem;
  box-sizing: border-box;
  justify-content: space-evenly;
}

.PowerStatsList i {
  color: white;
  margin: 0rem 0.5rem;
}
.PowerStatsList i:hover {
  color: red;
}
.Heading {
  width: 30%;
  text-align: center;
  background-image: linear-gradient(
    45deg,
    rgb(0, 119, 255) 0%,
    rgb(0, 119, 255) 60%,
    rgb(0, 153, 255) 60%
  );
  padding: 0.5rem;
  border-radius: 20px;
  color: white;
  margin-top: 1rem;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.3);
}
.PowerStatsList > div {
  width: 33%;
  box-sizing: border-box;
  padding: 1rem;
  margin: 0.5rem 0rem;
  background-image: linear-gradient(
    135deg,
    orange 0%,
    orange 50%,
    rgb(255, 145, 0) 50%
  );
  display: flex;
  align-items: center;
  margin: 0.5rem;
  border-radius: 10px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4), 8px 8px 8px rgba(0, 0, 0, 0.5);
}

.PublisherName {
  padding: 1rem;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  text-align: center;
  width: 50%;
  margin: 1rem auto;
  background-color: white;
}

@media screen and (max-width: 1000px) {
  .SuperHero {
    width: 100%;
    box-sizing: border-box;
  }
  .SuperHero___Image img {
    width: 90%;
    height: 300px;
    border-radius: 10px;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  }
}

@media screen and (max-width: 800px) {
  .SuperHero {
    width: 100%;
  }
  .SuperHero___Image img {
    width: 80%;
    height: 400px;
    height: 300px;
    border-radius: 10px;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  }
  .SuperHero___Image {
    width: 100%;
  }

  .SuperHero__Info {
    width: 100%;
  }

  .PowerStatsList > div {
    width: 80%;
  }
  .MoreInfo > div {
    width: 100%;
  }
}
